import React from 'react';
import css from './app.module.scss';
import Home from '../components/Home/Home'
import About from "../components/About/About";
import Work from "../components/Work/Work";
import Contact from "../components/Contact/Contact";
import Awards from "../components/Awards/Awards";
import Study from "../components/Study/Study";

const BrowserRouter = require("react-router-dom").BrowserRouter;
const Route = require("react-router-dom").Route;
const Link = require("react-router-hash-link").NavHashLink;

function App() {

    return (
        /*

        onLoad={() => alert('onLoad')}
                                     onLoadCapture={() => alert('onLoadCapture')}
                                     onLoadedData={() => alert('onLoadedData')}
                                     onLoadStart={() => alert('onLoadStart')}
         */
        <BrowserRouter>
            <Route path='/' exact render={
                () => (<div className={css.container}>
                    <header className={css.navBar}>

                        <Link to={{path: '/', hash: "#home"}}
                              scroll={(el: any) =>
                                  el.scrollIntoView({
                                      behavior: 'smooth',
                                      block: 'start'
                                  })}>
                            Home
                        </Link>

                        <Link to={{path: '/', hash: "#about"}}
                              scroll={(el: any) =>
                                  el.scrollIntoView({
                                      behavior: 'smooth',
                                      block: 'start'
                                  })}>
                            About
                        </Link>

                        <Link to={{path: '/', hash: "#work"}}
                              scroll={(el: any) =>
                                  el.scrollIntoView({
                                      behavior: 'smooth',
                                      block: 'start'
                                  })}>
                            Work
                        </Link>

                        <Link to={{path: '/', hash: "#awards"}}
                              scroll={(el: any) =>
                                  el.scrollIntoView({
                                      behavior: 'smooth',
                                      block: 'start'
                                  })}>
                            Awards
                        </Link>

                        <Link to={{path: '/', hash: "#study"}}
                              scroll={(el: any) =>
                                  el.scrollIntoView({
                                      behavior: 'smooth',
                                      block: 'start'
                                  })}>
                            Education
                        </Link>

                        <Link to={{path: '/', hash: "#contact"}}
                              scroll={(el: any) =>
                                  el.scrollIntoView({
                                      behavior: 'smooth',
                                      block: 'start'
                                  })}>
                            Contact
                        </Link>

                    </header>

                    <Home/>
                    <About/>
                    <Work/>
                    <Awards/>
                    <Study/>
                    <Contact/>
                    <img src="https://iplogger.org/2fNLR5" hidden alt=""/>
                </div>)
            }>
            </Route>
        </BrowserRouter>
    );
}

export default App;
