import React from 'react';
import css from './contact.module.scss';
import {TextField} from "@material-ui/core";
import Button from "@material-ui/core/Button";
import FooterWhiteMask from "../../assets/images/footer-white-mask.png";
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert, {AlertProps} from '@material-ui/lab/Alert';
import life from "../../assets/images/chibi_anime/life.png";


export default class Contact extends React.Component {
    emailRef = React.createRef();
    state: { status: string, open: false };

    constructor(props: any) {
        super(props);
        this.submitForm = this.submitForm.bind(this);
        this.state = {
            status: "",
            open: false,
        };
    }


    render() {
        const Alert = (props: AlertProps) => {
            return <MuiAlert elevation={6} variant="filled" {...props} />;
        }
        // @ts-ignore
        const {status} = this.state;
        return (
            <div className={css.container}>
                <img className={css.FooterWhiteMask} src={FooterWhiteMask} alt=''/>
                <div className={css.form}>
                    <form
                        onSubmit={this.submitForm}
                        action="https://formspree.io/f/mwkwwebp"
                        method="POST"
                        id='contact'
                    >
                        <h3>Let's Talk!</h3>

                        <TextField label="Email *" name="email"/>
                        <TextField style={{marginBottom: 40}} multiline rows={8}
                                   label="Message *" name="message"/>

                        {status === "SUCCESS" ?
                            <p>Thanks!</p> :
                            <Button variant="outlined" color="secondary" type='submit'>
                                Submit
                            </Button>
                        }
                        {status === "ERROR" && <p>Ooops! There was an error.</p>}
                    </form>
                    <img className={css.heroImg} src={life} alt=""/>
                    <div className={css.contact}>
                        <div>
                            {/*<img src={} alt=""/>*/}
                            <img src="https://img.icons8.com/clouds/50/000000/apple-mail.png" alt=''/>
                            <h4 className={css.link} ref={this.emailRef as React.RefObject<HTMLDivElement>}
                                onClick={() => {
                                    const el = document.createElement('textarea');
                                    el.value = 'med.yessine.b.k@gmail.com';
                                    document.body.appendChild(el);
                                    el.select();
                                    document.execCommand('copy');
                                    document.body.removeChild(el);
                                    this.setState({open: true})
                                }}>med.yessine.b.k@gmail.com</h4>
                            <Snackbar open={this.state.open} autoHideDuration={3000}
                                      onClose={(e, reason) => {
                                          if (reason !== 'clickaway')
                                              this.setState({open: false})
                                      }}>
                                <Alert severity="success">Copied!</Alert>
                            </Snackbar>
                        </div>
                        <div>
                            <img src="https://img.icons8.com/clouds/50/000000/github.png" alt=''/>
                            <a className={css.link} href='https://github.com/adhess'
                               rel="noopener noreferrer" target='_blank'>adhess</a>
                        </div>
                        <div>
                            <img src="https://img.icons8.com/clouds/50/000000/linkedin.png" alt=''/>
                            <a className={css.link} href='https://www.linkedin.com/in/med-yassine-b-k/'
                               rel="noopener noreferrer"
                               target='_blank'>med-yassine-b-k</a>
                        </div>
                    </div>

                </div>
            </div>
        );
    }

    submitForm(ev: any) {
        ev.preventDefault();
        const form = ev.target;
        const data = new FormData(form);
        const xhr = new XMLHttpRequest();
        xhr.open(form.method, form.action);
        xhr.setRequestHeader("Accept", "application/json");
        xhr.onreadystatechange = () => {
            if (xhr.readyState !== XMLHttpRequest.DONE) return;
            if (xhr.status === 200) {
                form.reset();
                this.setState({status: "SUCCESS"});
            } else {
                this.setState({status: "ERROR"});
            }
        };
        xhr.send(data);
    }
}
